/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 400px;
}

.logo {
  display: flex;
  flex-direction: column;
  position: absolute;
    top: 0%;
}

.logo img {
  height: 500px;
  width: 500px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  background-color: #aaf;
  padding: 1%;
  border-bottom: .1px solid #eee;
}

.navbar a {
  text-decoration: none;
  color: #555;
  margin: 0 4%;
  font-size: 20px;
  font-variant: small-caps;
  letter-spacing: 2px;
}

.navbar a:hover {
  text-decoration: overline;
}

.nav-right {
  margin-right: 4%;
  align-self: center;
}

.intro {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 600px;
  background-image: linear-gradient(#36D1DC, #5B86E5);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  clip-path: polygon(0 10%, 100% 0%, 100% 100%, 0 100%);
}

.description {
  display: flex;
  flex-direction: column;
  height: 70%;
  width: 40%;
  background-color: #fff;
  border: 1px solid inherit;
  border-radius: 8px;
}

.desc-title {
  text-align: center;
  padding-top: 4%;
  font-size: 24px;
  font-weight: 100;
  letter-spacing: 4px;
}

.desc-text {
  padding: 5%;
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-weight: 100;
  line-height: 2;
}

.testimonial {
  display: flex;
  flex-direction: column;
  height: 70%;
  width: 40%;
}

.user-thumbnail {
  display: flex;
  justify-content: center;
}

.thumbnail {
  border-radius: 100%;
}

.user-name {
  display: flex;
  justify-content: center;
  padding-top: 4%;
  color: #fff;
  font-weight: 100;
  font-size: 20px;
}

.user-comment {
  display: flex;
  justify-content: center;
  height: 30%;
  padding-top: 4%;
}

.user-comment p {
  text-align: center;
  color: #fff;
  font-size: 20px;
  font-weight: 100;
  letter-spacing: 2px;
  line-height: 1.5;
}

.more-reviews {
  display: flex;
  justify-content: center;
  padding-top: 8%;
}

.reviews-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000;
  background-color: #77f;
  border: 1px solid #77f;
  border-radius: 4px;
  height: 40px;
  width: 150px;
  font-weight: 300;
  font-size: 16px;
  font-variant: small-caps;
  letter-spacing: 2px;
  box-shadow: 1px 1px 4px #77f;
}

.reviews-btn:hover {
  background-color: #44f;
  border-color: #44f;
  box-shadow: 1px 1px 1px #44f;
  cursor: pointer;
}

.features {
  height: 500px;
  padding: 6%;
  display: flex;
  flex-direction: column;
}

.features h1 {
  text-align: center;
  font-size: 58px;
  font-weight: 100;
  letter-spacing: 2px;
}

.feat-types {
  padding-top: 10%;
  display: flex;
}

.likes {
  padding-top: 4%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.likes i {
  align-self: center;
  color: #f44;
}

.likes p {
  align-self: center;
  width: 80%;
  padding-top: 6%;
  font-size: 30px;
  font-weight: 100;
  letter-spacing: 2px;
  line-height: 1.5;
}

.cause {
  padding-top: 4%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.cause i {
  align-self: center;
  color: #447;
}

.cause p {
  align-self: center;
  width: 80%;
  padding-top: 6%;
  font-size: 30px;
  font-weight: 100;
  letter-spacing: 2px;
  line-height: 1.5;
}

.share {
  padding-top: 4%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.share i {
  align-self: center;
  color: #555;
}

.share p {
  align-self: center;
  width: 80%;
  padding-top: 6%;
  font-size: 30px;
  font-weight: 100;
  letter-spacing: 2px;
  line-height: 1.5;
}

.pricing {
  height: 600px;
  padding: 4%;
  background-color: #eee;
  display: flex;
  flex-direction: column;
}

.pricing h1 {
  text-align: center;
  font-size: 58px;
  font-weight: 100;
  letter-spacing: 2px;
}

.price-tiers {
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.price-tiers h2 {
  text-align: center;
  padding-top: 4%;
  font-size: 34px;
  font-weight: 100;
  letter-spacing: 2px;
  font-variant: small-caps;
}

.lite {
  width: 30%;
  height: 60%;
  border: 1px solid #fff;
  border-right-color: silver;
  background-color: #fff;
  border-radius: 5px;
}

.future-leader {
  width: 35%;
  height: 80%;
  border: 1px solid #fff;
  background-color: #fff;
  border-radius: 5px;
}

.foot-soldier {
  width: 30%;
  height: 60%;
  border: 1px solid #fff;
  border-left-color: silver;
  background-color: #fff;
  border-radius: 5px;
}

.price-tiers p {
  text-align: center;
  padding: 10% 2% 2% 2%;
  font-size: 20px;
  font-weight: 100;
  line-height: 2;
}

footer {
  height: 150px;
  background-color: #48f;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer h4 {
  font-size: 30px;
  font-weight: 100;
  letter-spacing: 2px;
  color: #fff;
}

